@import "../../styles/baseStyles";

.profile-container {
	@include flexCenter(flex, center, center);
	margin-top: 200px;
	width: 100%;
	height: fit-content;
	padding-bottom: 360px;
	background-image: url(../../images/wave3.svg);
	background-size: cover;
	background-repeat: no-repeat;

	.profile {
		width: 33%;
		margin-bottom: 100px;

		color: $primary-color;
		font-size: 25px;
		font-weight: 100;
		word-spacing: 2px;
		line-height: 1.5;

		h3 {
			padding-top: 30px;
			font-weight: 300;
			font-size: 50px;
			line-height: 1.3;
			margin: 0;
			padding-bottom: 20px;
		}
		p {
			margin: 0;
			padding-top: 20px;
			word-spacing: 3px;
			line-height: 45px;
		}
	}
	// }
}

@media (max-width: 400px) {
	.profile-container {
		.profile {
			grid-template-columns: 100%;
			width: 80%;
			.profile-picture {
				@include flexCenter(flex, center, center);

				.picture {
					width: 200px;
					height: 200px;
				}
			}
		}
		.profile-description {
			h3 {
				text-align: center;
				font-size: 30px;
			}
			p {
				font-size: 20px;
			}
		}
	}
}
@media (min-width: 300px) and (max-width: 520px) {
	.profile-container {
		background-image: none;
		padding-bottom: 0;
	}
}

@media (min-width: 401px) and (max-width: 600px) {
	.profile-container {
		.profile {
			grid-template-columns: 100%;
			width: 70%;
			.profile-picture {
				@include flexCenter(flex, center, center);

				.picture {
					width: 200px;
					height: 200px;
				}
			}
		}
		.profile-description {
			h3 {
				text-align: center;
				font-size: 30px;
			}
			p {
				font-size: 20px;
			}
		}
	}
}
@media (min-width: 601px) and (max-width: 1300px) {
	.profile-container {
		.profile {
			width: 70%;
			grid-template-columns: 100%;
		}
		.profile-picture {
			@include flexCenter(flex, center, center);
		}
		.profile-description {
			padding: 0 20px 0 20px;

			h3 {
				text-align: center;
				font-size: 30px;
			}
			p {
				font-size: 20px;
			}
		}
	}
}

@media (min-width: 901px) and (max-width: 1600px) {
	.home-container {
		.profile {
			grid-template-columns: 100%;

			.profile-picture {
				@include flexCenter(flex, center, center);
			}
			.profile-description {
				padding: 0 20px 0 20px;
			}
		}
	}
}
